
import { PropType, defineComponent } from 'vue'
import { IbUser, WithdrawAccount } from "@/views/cib/cib-interface/ib-subscription-edit.interface"
import { CIB20100312_REQ_BODY, CIB20100312_RES} from '@/shared/type/CIB20100312.type'
import { CIB20100336_REQ_BODY, CIB20100336_RES } from '@/shared/type/CIB20100336.type'
import { CIB20100311_REQ_BODY, CIB20100311_RES, IbUserDetail} from '@/shared/type/CIB20100311.type'
import { modalController } from '@ionic/vue'
import { COMMON_KEY_VALUE } from '@/shared/constants/common.const';
import { screenMode } from "@/shared/type/Approval.type";
import { AuthorizationDetail } from '@/shared/type/APM20200113.type';

export default defineComponent({
  name: "cib201003ss06",
  props: {
    dataIB: {
      type: Object as PropType<IbUser>,
      required: true,
    },
    currentTab: {
      type: String,
      required: true,
    },
    approvalPayload: {
      type: Object as PropType<AuthorizationDetail>,
      required: false
    }
  },

  data() {
    const userID = this.$route.query.userID;
    const customerNo = this.$route.query.customerNo;
    const approvalTap = this.$route.query.tab;
    return {
      userID,
      customerNo,
      approvalTap,
      COMMON_KEY_VALUE,
      withdrawAccounts: [] as WithdrawAccount[],
      tempWithdrawAccounts: [] as WithdrawAccount[],
      customerDetail: {} as IbUserDetail,
      isNotAllowEditMainTransferAccount: [] as boolean[],
      isDisable:true,
      isEditable: false,
      isDisableDesignated: true,
      accountType: "",
      mainTransferAccount: "",
      mainAccountNo:"",
      accountlistDesignated: [] as { accountNo: string, deleteYN: string, accountTypeCode:string, designatedAccountTypeCode: string }[],
      approvalRes: {} as any,
			operationMode: 'normal' as screenMode
    };
  },

  // async mounted(){
  //   this.getWithdrawalAccount();
  //   await this.onGetUserSubscriptionDetail();
  // },

  watch: {
    approvalPayload(payload:AuthorizationDetail){
      if(this.approvalTap == 'CIB201003SS06'){
        let authDetailParsed = JSON.parse(payload.requestContent)
        this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal';
        this.withdrawAccounts = authDetailParsed.accountList;
        this.accountType = authDetailParsed.accountManagementTypeCode;
        this.mainTransferAccount = authDetailParsed.mainTransferAccount;
        this.withdrawAccounts.map((item)=>{
          if(authDetailParsed.accountManagementTypeCode == '02' && item.designatedAccountTypeCode == '02'){
            item.isInquiry = true;
          }else if (authDetailParsed.accountManagementTypeCode == '02' && item.designatedAccountTypeCode == '01'){
            item.isWithdraw = true;
          }else if (item.designatedAccountTypeCode == ''){
            item.isInquiry = false;
            item.isWithdraw = false;
          }else{
            item.isInquiry = true;
            item.isWithdraw = true;
          }
        })
      }
    },

    dataIB(customerData: IbUserDetail){
      if(this.approvalType == ''){
        this.mainTransferAccount = customerData.userInfo.mainTransferAccount;
        this.accountType = customerData.userInfo.accountManagementTypeCode;
      }
    },

    'accountType' : function(designatedAccountTypeCode) {
      if(this.approvalType == ''){
        switch (designatedAccountTypeCode) {
          case "01":
            this.isDisableDesignated = true;
            this.handleAccountManagement(designatedAccountTypeCode, this.mainTransferAccount);
            break;
          case "02":
            if(this.isDisable == false){
              this.isDisableDesignated = false;
            }
            this.handleAccountManagement(designatedAccountTypeCode, this.mainTransferAccount);
            break;
        }
      }
    },

    async currentTab(tabTittle: string){
      if(this.approvalType == ''){
        if(tabTittle == 'cib201003ss06'){
          await this.onGetUserSubscriptionDetail();
          this.mainTransferAccount =  this.customerDetail.userInfo.mainTransferAccount;
          this.accountType = this.customerDetail.userInfo.accountManagementTypeCode;
          this.getWithdrawalAccount();
        }
      }
    },

    isEditable(isAllowTabChange) {
      this.$emit('isBeingEdited', isAllowTabChange);
      // if(!isAllowTabChange){
        // this.tempWithdrawAccounts =  this.withdrawAccounts
      // }
    }
  },

  computed: {
		approvalType(): string {
			return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : '';
		},
  },

  methods: {
    async getWithdrawalAccount(){
      this.isDisableDesignated = true;
      const reqBody: CIB20100312_REQ_BODY = {
        customerNo: `${this.customerNo}`,
        userID: `${this.userID}`,
      };
      const responseData = await this.$request.getRequest('CIB20100312', reqBody, false) as CIB20100312_RES;

      if (responseData.header.result) {
        this.withdrawAccounts = JSON.parse(JSON.stringify( responseData.body.list));
        this.tempWithdrawAccounts = JSON.parse(JSON.stringify( responseData.body.list));
      } 

    this.isNotAllowEditMainTransferAccount = []
    this.handleMaintransferAccont(this.accountType ,this.withdrawAccounts)
  },

  handleMaintransferAccont(type:string, account: WithdrawAccount[]){
    account.map((item)=>{
      if(type == '01'){
        if(item.designatedAccountTypeCode == '02'){
          item.isInquiry = true;
        }else{
          item.isWithdraw = true;
        }
      }else{
        if(item.accountNo == this.mainTransferAccount){
          if(item.designatedAccountTypeCode == '02'){
            item.isInquiry = true;
          }else{
            item.isWithdraw = true;
          }
          this.isNotAllowEditMainTransferAccount.push(true);
        }else{
          if(item.designatedAccountTypeCode == '02'){
            item.isInquiry = true;
          }else if(item.designatedAccountTypeCode == '01'){
            item.isWithdraw = true;
          }else if(item.designatedAccountTypeCode == ''){
            item.isWithdraw = false;
            item.isInquiry = false;
          }
          this.isNotAllowEditMainTransferAccount.push(false);
        }
      }
    })
  },

  async onGetUserSubscriptionDetail() {
		const reqBody: CIB20100311_REQ_BODY = {
			userID: `${this.$route.query.userID}`,
			customerNo: `${this.$route.query.customerNo}`
		};
		// ---- Adapter: CIB20100311_Adapter_IBUserSubscription_InquiryDetail
		const responseData = await this.$request.getRequest('CIB20100311', reqBody, false) as CIB20100311_RES

		if (responseData.header.result) {
			this.customerDetail = JSON.parse(JSON.stringify(responseData.body));
		} 
	},

  onClickOperation(btn: string) {
    switch (btn) {
      case "edit": 
        this.isEditable = true;
        this.isDisable = false;
        if(this.accountType == '02'){
          this.isDisableDesignated = false;
        }
        break;

      case "cancel":
        this.isEditable = false;
        this.isDisable = true;
        this.isDisableDesignated = true;
        this.getWithdrawalAccount();
        break;

      case "list":
        this.$router.push('/corporate-internet-banking/subscription');
        break;

      case "submit":
        this.$dialog.onOpen({
          content: `${this.$t("message.would_you_like_to_save")}`,
          dialogMode:"Question",
          callback: ()=>{
            this.handleSubmit();
            modalController.dismiss();
          }
        });
        break;
      }
    },

    async handleSubmit() {
      this.accountlistDesignated = [];
      this.withdrawAccounts.map((item)=>{
        if( item.isInquiry == true && item.isWithdraw == true){
          item.designatedAccountTypeCode = "01";
        }else if(item.isInquiry == true){
          item.designatedAccountTypeCode = "02";
        }else if(item.isWithdraw == true){
          item.designatedAccountTypeCode = "01";
        }else if(!item.isInquiry || !item.isInquiry){
          item.designatedAccountTypeCode = "";
        }
        this.accountlistDesignated.push({
          accountNo: item.accountNo,
          designatedAccountTypeCode: item.designatedAccountTypeCode,
          deleteYN: 'N',
          accountTypeCode: item.accountTypeCode
        })
      });

      const body: CIB20100336_REQ_BODY = {
        userID: `${this.userID}`,
        customerNo: `${this.customerNo}`,
        accountList: this.accountlistDesignated,
        accountManagementTypeCode: this.accountType,
        mainTransferAccount: this.mainTransferAccount
      };
    
      const resWithdrawalAccount = await this.$request.getRequest('CIB20100336', body, false, 'CIB201003SS06') as CIB20100336_RES;

      if(this.approvalType){
        this.isDisableDesignated = true;
        this.isDisable = true;
        this.isEditable = false;
        this.$emit("isReseTButton", false);
      }

      if(resWithdrawalAccount.header.result){
        this.isDisableDesignated = true;
        this.isDisable = true;
        this.isEditable = false;
        this.tempWithdrawAccounts = [];
        this.getWithdrawalAccount();
        this.$emit("isReseTButton", false);
        this.$dialog.onOpen({
          content: `${this.$t("message.update_success")}`,
          dialogMode:'Success',
          callback: ()=>{
            modalController.dismiss();
          }
        })
      }
    },

    handleAccountManagement(accountManagementTypeCode: string ,mainTransferAccount: string){
      if(accountManagementTypeCode == '01'){
        this.withdrawAccounts.map((account) => {         
          if((account.isWithdraw &&  account.isInquiry) && (account.designatedAccountTypeCode == '00' || account.designatedAccountTypeCode == '')){
            account.isWithdraw = true;
            account.isInquiry = false;
            this.isNotAllowEditMainTransferAccount.push(false);
          }else{
            this.isNotAllowEditMainTransferAccount.push(false);
            account.isWithdraw = true;
            account.isInquiry = false;
          }
        })
      }else{
        this.withdrawAccounts.map((account) => {
          if(account.accountNo == mainTransferAccount){
            account.isWithdraw = true;
            account.isInquiry = false;
            this.isNotAllowEditMainTransferAccount.push(false);
          }
          else{
            this.tempWithdrawAccounts.map((item)=>{
              if(item.designatedAccountTypeCode == '02'){
                item.isInquiry = account.isInquiry = true;
                item.isWithdraw = account.isWithdraw = false; 
              }else if (item.designatedAccountTypeCode == '01'){
                item.isInquiry = account.isInquiry = false;
                item.isWithdraw = account.isWithdraw = true;
              }
            })
          } 
        })
      }
    },

    isInquiryChange(index:number){
      if(this.accountType == '01'){
        if (this.withdrawAccounts[index].isInquiry == true) {  
          this.withdrawAccounts[index].isInquiry = false;  // 01 check only full access not both
          this.withdrawAccounts[index].isWithdraw = true;
          this.withdrawAccounts[index].designatedAccountTypeCode = "01";
        }
      }else{
        if (this.withdrawAccounts[index].isInquiry == true) {  
          this.withdrawAccounts[index].isInquiry = true;  
          this.withdrawAccounts[index].isWithdraw = false;
          this.withdrawAccounts[index].designatedAccountTypeCode = "02";
        }
      }
    },

    isWithdrawChange(index:number){
      if(this.accountType == '01'){
        if (this.withdrawAccounts[index].isWithdraw == true) { 
          this.withdrawAccounts[index].isWithdraw = true; 
          this.withdrawAccounts[index].isInquiry = false; // 01 check only full access not both
          this.withdrawAccounts[index].designatedAccountTypeCode = "01";
        }
      }else{
        if (this.withdrawAccounts[index].isWithdraw == true) { 
          this.withdrawAccounts[index].isWithdraw = true; 
          this.withdrawAccounts[index].isInquiry = false;
          this.withdrawAccounts[index].designatedAccountTypeCode = "01";
        }
      } 
    },
  }
})
